import React from 'react'

export const AboutMe = () => (
    <section id="about-me">
        <div className="container">
            <h1 className="title-with-icon hidden" data-scroll>About Me</h1>
            <div className="jumbocont mb-5">  
                <div className="row">
                    <div id="profile" className="column-lg-4 text-center">
                        <img className="n-concave morph-frame morph-flat mb-4" alt="mytabwork" src="/images/personal/about.jpg" />
                    </div>
                    <div id="content" className="column-md-12 column-lg-8 n-flat">
                        <div className="row">
                            <div className="column-md-6">
                                <p className="context">I'm a Web Developer graduated in Tarlac State University. 
                                I enjoy taking challenging and complex problems, and if, therefore, promising I turned them into 
                                a delightful and resuable libraries. I'am also passionate in logic and the 
                                structure of coding, and always strive to write clean, elegant, and 
                                effecient code.</p>
                            </div>
                            <div className="column-md-6">
                                <div className="progress-wrapper">
                                    <span className="progress-label">Front-end Web Development</span>
                                    <div className="n-progress">
                                        <div className="n-progress-bar hidden n-warning" data-percentage="83%" data-scroll style={{width: '83%'}}/>
                                    </div>
                                </div>
                                <div className="progress-wrapper">
                                    <span className="progress-label">Back-end Web Developement</span>
                                    <div className="n-progress">
                                    <div className="n-progress-bar hidden" data-percentage="78%" data-scroll style={{width: '78%'}}/>
                                    </div>
                                </div>
                                <div className="progress-wrapper">
                                    <span className="progress-label">Basketball</span>
                                    <div className="n-progress">
                                        <div className="n-progress-bar hidden n-danger" data-percentage="95%" data-scroll style={{width: '95%'}}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="column-6 column-sm-3">
                    <div className="icon-card-wrapper">
                        <div className="icon-card n-flat n-bordered">
                            <i className="mytabworks icon-task" />
                        </div>
                        <h2 className="icon-card-title">Projects</h2>
                        <h2 data-number>20</h2>
                    </div>
                </div>
                <div className="column-6 column-sm-3">
                    <div className="icon-card-wrapper">
                        <div className="icon-card n-flat n-bordered">
                            <i className="mytabworks icon-githead" />
                        </div>
                        <h2 className="icon-card-title">Repository</h2>
                        <h2 data-number>12</h2>
                    </div>
                </div>
                <div className="column-6 column-sm-3">
                    <div className="icon-card-wrapper">
                        <div className="icon-card n-flat n-bordered">
                            <i className="mytabworks icon-package" />
                        </div>
                        <h2 className="icon-card-title">Packages</h2>
                        <h2 data-number>10</h2>
                    </div>
                </div>
                <div className="column-6 column-sm-3">
                    <div className="icon-card-wrapper">
                        <div className="icon-card n-flat n-bordered">
                            <i className="mytabworks icon-rocket" />
                        </div>
                        <h2 className="icon-card-title">Project Publish</h2>
                        <h2 data-number>10</h2>
                    </div>
                </div>
            </div>
        </div>
    </section>
)