import React from 'react';
export const Skills = ({ facade = 'data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw=='}) => (
	<section id="skills">
		<div className="container">
			<h1 className="title-with-icon hidden" data-scroll>Skills</h1>
			<div className="row">
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='html' src={facade} data-src='/images/tools/html.png' />
                        </div>
                        <h2 className="icon-card-title">HTML 5</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='css' src={facade} data-src='/images/tools/css.png' />
                        </div>
                        <h2 className="icon-card-title">CSS 3</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='js' src={facade} data-src='/images/tools/js.png' />
                        </div>
                        <h2 className="icon-card-title">JS</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='react' src={facade} data-src='/images/tools/react-logo192.png' />
                        </div>
                        <h2 className="icon-card-title">react</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='git' src={facade} data-src='/images/tools/git.png' />
                        </div>
                        <h2 className="icon-card-title">git</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10 of-hidden">
							<img alt='typescript' src={facade} data-src='/images/tools/ts.png' />
                        </div>
                        <h2 className="icon-card-title">TypeScript</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='nodejs' src={facade} data-src='/images/tools/node.png' />
                        </div>
                        <h2 className="icon-card-title">nodejs</h2>
                    </div>
				</div>
				<div className="column-6 column-md-4 column-lg-3">
					<div className="icon-card-wrapper morph-engrave n-engrave">
                        <div className="icon-card morph-flat n-flat n-bordered br-10">
							<img alt='php' src={facade} data-src='/images/tools/php.png' />
                        </div>
                        <h2 className="icon-card-title">PHP</h2>
                    </div>
				</div>
			</div>
		</div>
	</section>
)
