import React from 'react';
import { useForm } from 'formydable';
import request from 'requestjs';
import Toast from 'Snippets/Toast';
import { Spinner } from 'Snippets/Spinner';
import { uid } from 'Common/Utils';
import { GoogleAppURL } from 'Common/API';

export const Contact = () => {
	const { formState, formUpdate, formSubmit } = useForm({
		name: { label: 'Name', rules: 'required|alpha_space|min:3|max:20' },
		email: { label: 'E-mail', rules: 'required|email' },
		subject: { label: 'Subject', rules: 'required|min:3|max:20' },
		message: { label: 'Message', rules: 'required|min:10' },
	});
	const { name, email, subject, message } = formState();
	const submitHandler = formSubmit((event) => {
		const sssupported = 'sessionStorage' in window;
		if (sssupported && window.sessionStorage.getItem('alread-send-email')) {
			return Toast.create(
				'Email has already been sent and we will response immidiately.'
			);
		}
		if (event.isReady()) {
			const key = uid();
			Toast.create(
				<div className="d-flex flex-center-y">
					<Spinner loading={true} />
					sending...
				</div>,
				{ key, options: { duration: 20000 } }
			);

			request(GoogleAppURL, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				responseType: 'json',
				body: event.param()+'&action=contact'
			})
			.then((response) => {
				if(response.data.status !== "success") {
					throw Error()
				}
			})
			.then(() => 
				Toast.create(
					<div className="d-flex flex-center-y">
						<Spinner success={true} />
						Successfully send a e-mail
					</div>,
					{ key, options: { duration: 4000 } }
				)
			)
			.catch(() =>
				Toast.create(
					<div className="d-flex flex-center-y">
						<Spinner success={false} />
						something went wrong try again later
					</div>,
					{ key, options: { duration: 4000 } }
				)
			);
			event.reset();
			sssupported && window.sessionStorage.setItem('alread-send-email', true);
		}
	});
	return (
		<section id='contact-me'>
			<div className='container'>
				<h1 className='title-with-icon hidden' data-scroll>
					Contact Me
				</h1>
				<div className='jumbocont n-flat n-bordered of-hidden'>
					<div className=''>
						<div className='row'>
							<div className='column-sm-4'>
								<div className='icon-card-wrapper'>
									<div className='icon-card n-flat n-bordered'>
										<i className='mytabworks icon-whatsapp' />
									</div>
									<h2 className='icon-card-title'>WhatsApp</h2>
									<p>+63 930 735 2127</p>
								</div>
							</div>
							<div className='column-sm-4'>
								<div className='icon-card-wrapper'>
									<div className='icon-card n-flat n-bordered'>
										<i className='mytabworks icon-phone' />
									</div>
									<h2 className='icon-card-title'>Call</h2>
									<p>+63 930 735 2127</p>
								</div>
							</div>
							<div className='column-sm-4'>
								<div className='icon-card-wrapper'>
									<div className='icon-card n-flat n-bordered'>
										<i className='mytabworks icon-mail' />
									</div>
									<h2 className='icon-card-title'>E-mail</h2>
									<p>mytabworks@gmail.com</p>
								</div>
							</div>
						</div>
					</div>
					<div className='pt-5'>
						<div className='row'>
							<div className='column-lg-4'>
								<div className='gigantic'>
									<h3 className='gigantic-context mb-5'>
										Let's talk business{' '}
										<i className='mytabworks icon-paper-plane' />
									</h3>
								</div>
							</div>
							<div className='column-lg-8'>
								<form onSubmit={submitHandler}>
									<div className='row'>
										<div className='column-sm-6'>
											<div
												className={`n-form-field${
													name.isInvalid
														? ' n-form-invalid'
														: name.isValidated
														? ' n-form-valid'
														: ''
												}`}>
												<input
													className='n-form-control'
													type='text'
													name='name'
													placeholder='Name...'
													value={name.value}
													onChange={formUpdate}
												/>
												{name.isInvalid && (
													<div className='n-invalid-feedback'>
														{name.message}
													</div>
												)}
												<div className='n-valid-feedback'>
													<span role='img'>✔️</span> Looks Great
												</div>
											</div>
										</div>
										<div className='column-sm-6'>
											<div
												className={`n-form-field${
													email.isInvalid
														? ' n-form-invalid'
														: email.isValidated
														? ' n-form-valid'
														: ''
												}`}>
												<input
													className='n-form-control'
													type='email'
													name='email'
													placeholder='E-mail address...'
													value={email.value}
													onChange={formUpdate}
												/>
												{email.isInvalid && (
													<div className='n-invalid-feedback'>
														{email.message}
													</div>
												)}
												<div className='n-valid-feedback'>
													<span role='img'>✔️</span> Looks Great
												</div>
											</div>
										</div>
									</div>
									<div
										className={`n-form-field${
											subject.isInvalid
												? ' n-form-invalid'
												: subject.isValidated
												? ' n-form-valid'
												: ''
										}`}>
										<input
											className='n-form-control'
											type='text'
											name='subject'
											placeholder='Subject... '
											value={subject.value}
											onChange={formUpdate}
										/>
										{subject.isInvalid && (
											<div className='n-invalid-feedback'>
												{subject.message}
											</div>
										)}
										<div className='n-valid-feedback'>
											<span role='img'>✔️</span> Looks Great
										</div>
									</div>
									<div
										className={`n-form-field${
											message.isInvalid
												? ' n-form-invalid'
												: message.isValidated
												? ' n-form-valid'
												: ''
										}`}>
										<textarea
											className='n-form-control'
											name='message'
											placeholder='Message...'
											value={message.value}
											onChange={formUpdate}
										/>
										{message.isInvalid && (
											<div className='n-invalid-feedback'>
												{message.message}
											</div>
										)}
										<div className='n-valid-feedback'>
											<span role='img'>✔️</span> Looks Great
										</div>
									</div>
									<button className='n-btn n-btn-lg mt-2 br-50' type='submit'>
										<i className='mytabworks icon-paper-plane' />
										&nbsp;Submit
									</button>
								</form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	);
};
