import React, { useState, useEffect } from 'react';
import { useScroll } from 'Snippets/Animation'
import {
    is,
	css,
	addHiddenEventListener,
	removeHiddenEventListener,
	keyHiddenEvent,
} from 'Common/Utils';

const waveLength = (x, y) => {
	const div = document.createElement('div');
	document.body.appendChild(div);
	div.className = 'wave-length wave active';
	div.ontransitionend = () => {
		div.remove();
	};
	css(div, {
		position: 'absolute',
		left: `${x - div.offsetWidth / 2}px`,
		top: `${y - div.offsetHeight / 2}px`,
	});
	setTimeout(() => {
		css(div, { transform: 'scale(1)', opacity: 0 });
	}, 50);
};

export const Home = () => {
	const [state, setState] = useState(false);
 
	useEffect(() => {
		const prefixTop = 99
		const home = document.querySelector('#home')
		const root = document.documentElement
		if(!home) return
		let status = state
		const handler = () => {
			const wTop = root.scrollTop 
			const { y, height } = home.getBoundingClientRect();
			const windowPos = Math.floor(wTop + prefixTop);
			const targetPos = Math.floor(wTop + y);
			const targetViewPos = targetPos + height;
			const withinTarget = targetPos <= windowPos && targetViewPos >= windowPos
			if (withinTarget && status) {
				status = false
				setState(status)
			} else if(!withinTarget && !status) {
				status = true
				setState(status)
			} 
		}
		window.addEventListener('scroll', handler)
		return () => {
			window.removeEventListener('scroll', handler)
		} 
	}, [state])

	useEffect(() => {
		if (state) return;
		let cleanup;
		const heading = document.getElementById('home');
		const handler = () => { 
			if (!document[keyHiddenEvent] || is.und(keyHiddenEvent)) {
				waveLength(
					Math.floor(Math.random() * heading.offsetWidth),
					Math.floor(Math.random() * heading.offsetHeight)
				);
				cleanup = setInterval(
					() =>
						waveLength(
							Math.floor(Math.random() * heading.offsetWidth),
							Math.floor(Math.random() * heading.offsetHeight)
						),
					5000
				);
			} else {
				clearInterval(cleanup);
			}
        };
        handler()
		addHiddenEventListener(handler);
		return () => {
			removeHiddenEventListener(handler);
			clearInterval(cleanup);
		};
	}, [state]);

	const handleClick = (e) => {
		const root = document.documentElement;
		waveLength(root.scrollLeft + e.clientX, root.scrollTop + e.clientY);
		if (!state) {
			setState(true);
		}
	};
	return (
		<section
			onClick={handleClick}
			id='home'>
			<div className='container'>
				<div className='intro'>
					<div className='profile n-flat n-bordered'>
						<img
							alt='beard-guy'
							src='/images/personal/profile.png'
						/>
					</div>
					<h2 className='full-name'>Fernando Tabamo Jr.</h2>
					<h3 className='work-status'>I'am Web Developer</h3>
					<nav className='social-media'>
						<a className='n-btn b-round' href='https://www.linkedin.com/in/fernando-tabamo-jr-501718169/'>
							<i className='mytabworks icon-linkedin' />
						</a>
						<a className='n-btn b-round' href='https://github.com/mytabworks'>
							<i className='mytabworks icon-github-square' />
						</a>
						<a className='n-btn b-round' href='https://www.facebook.com/Mytabworks-789472761443419'>
							<i className='mytabworks icon-fb' />
						</a>
					</nav>
				</div>
			</div>
			<div className="scroll-down">scroll down</div>
		</section>
	);
};
