import React, { useEffect, useRef } from 'react'
import { Link } from 'reaouter';
import { SmoothScroll, useScrollSpy } from 'Snippets/Animation'
import { Navibar } from 'Templates'
import N from 'Source/App/Main/Pages/Neumorphism/scripts'

export const NavBar = () => {
    const navigation = useRef(null)

    useScrollSpy([
        'home',
        'about-me',
        'practice',
        'skills',
        'contact-me'
    ], '', '.n-nav', 200)

    useEffect(() => {
        console.log(navigation)
        if(!navigation.current) return;
        const dropdowns = Array.from(navigation.current.querySelectorAll('#navigation-bar .n-dropdown'))
        const instances = dropdowns.map(dropdown => N.dropdown.getInstance(dropdown))
		return () => instances.forEach(instance => instance.destroy());
    }, [navigation])

    return (<div ref={navigation}>
    <Navibar id="navigation-bar" className="fixed-top n-flat n-bordered">
        <div className="container">
            <SmoothScroll className="navibar-brand" href="#home" label={<>
                <img className="navibar-brand-img" src="/images/personal/brand.png" alt="mytabworks" /> 
                <span data-color-changable>Mytabworks</span>
            </>} /> 
            <nav className="n-nav n-no-hover">
                <SmoothScroll className="n-nav-link" href="#home" label="Home" />
                <SmoothScroll className="n-nav-link" href="#about-me" label="About Me" />
                <button className="navibar-toggle n-btn" data-collapse="#navibar-collapse" aria-expanded="false">&bull;&bull;&bull;</button>
            </nav>
            <div className="navibar-collapse" id="navibar-collapse">
                <nav className="n-nav n-no-hover">
                    <SmoothScroll className="n-nav-link" href="#practice" label="Practice" />
                    <SmoothScroll className="n-nav-link" href="#skills" label="Skills" />
                    <SmoothScroll className="n-nav-link" href="#contact-me" label="Contact Me" />
                </nav>
                <nav className="n-nav n-no-hover ml-auto">  
                    <div className="n-dropdown" data-interactive="true"> 
                        <a 
                            className="n-dropdown-toggle n-nav-link" 
                            href="#">
                            <i className="mytabworks icon-task"/>
                            Works
                        </a>
                        <div className="n-dropdown-menu">
                            <div className="n-dropdown-content">
                                <Link to="/react" label={'React libraries'} className="n-dropdown-item" />
                                <Link to="/javascript" label={'Javascript libraries'} className="n-dropdown-item" />
                                <Link to="/neumorphism" label={'Neumorphism project'} className="n-dropdown-item" />
                            </div>
                        </div>
                    </div> 
                </nav>
            </div>
        </div>
    </Navibar></div>
)
}