import React from 'react'
import { Link, Route, useLocation, SubRouter } from 'reaouter';

export const Tabs = () => {
    const { path, url } = useLocation()
    return (
        <>
        <div className="container p-2">
                <div className="d-flex flex-center-x mt-5">
                    <div className="n-flat n-bordered b-round of-hidden p-3">
                        <img alt="beard" src="/images/icons/icon-512x512.png" style={{maxWidth: '200px'}} />
                    </div>
                </div>
                <div className="n-flat n-bordered mt-5 p-2">
                    <h1 className="t-center mb-4">Fernando Tabamo Jr.</h1> 
                    <SubRouter path={path} render={<Tabs />} />
                </div> 
            </div>
            <div className="d-flex">
                <Link className="n-btn n-btn-lg w-full" to={`${url}`} label="Skills" />
                <Link className="n-btn n-btn-lg w-full" to={`${url}/about-me`} label="About me"/>
                <Link className="n-btn n-btn-lg w-full" to={`${url}/contact`} label="Contact"/>
            </div>
            <Route path={`${path}`} render={<>skills</>} />
            <Route path={`${path}/about-me`} render={<>about me</>} />
            <Route path={`${path}/contact`} render={<>contact me</>} />
        </>
    )
}